.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  /* background: #183883; */
  /* background: #b9bda8; */
  background: #485b6b;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
  justify-content: space-between;
}

.main-header-dark {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  /* background: #737272; #20232a; */
  /* background: #4398af; */
  background: #c2bbbb;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
  justify-content: space-between;
}

main {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
