.authentication__header {
  color: white;
  text-align: center;
}

.authentication__error {
  color: red;
  margin: 20px 0;
  font-size: medium;
}

.authentication__success {
  color: blue;
  margin: 20px 0;
  font-size: medium;
}

.authentication {
  width: 90%;
  max-width: 30rem;
  margin: 5rem auto;
  text-align: center;
  /*background: transparent;*/
}

.authentication form {
  margin-bottom: 1rem;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.authentication__form__login.button {
  margin: 5rem 5rem 2rem 5rem;
  width: 100%;
  background-color: #183883;
  border-style: solid;
  border: 2px solid #183883;
  font-weight: 600;
  font-size: 1.2rem;
}

.login-template {
  flex-basis: 50%;
  border-radius: 10px;
  background: linear-gradient(
    to left,
    #feac5e,
    #c779d0,
    #4bc0c8 100%
  ) !important;
  flex-direction: column;
  width: 400px;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid lightgray;
}

.register {
  width: 85%;
  /* max-width: 55rem; Auth2*/
  max-width: 30rem;
  margin: 5rem auto;
  text-align: center;
}

.register form {
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
