.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 0.5rem;
}

.nav-links a {
  border: 1px solid transparent;
  /* color: white; */
  color: rgb(122, 121, 121);
  text-decoration: none;
  padding: 0.5rem;
  font-size: large;
  font-weight: 1000;
  /* margin: 1rem; */
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  /* color: #f72585; */
  color: white;
  font-weight: 1000;
  font-size: large;
  text-shadow: 0 0 0.1em white;
  /* text-shadow: 0 0 0.2em white, 0 0 0.2em white; */
}

.dark a.active {
  /* background: #f8df00;
  color: #292929; */
  /* background: #183883;
  border-color: #292929;
  color: white;
  font-weight: bolder; */
  /* background: #b6c0d8; */
  color: #183883;
  font-weight: 1000;
  font-size: large;
  /* text-shadow: 0 0 0.2em white, 0 0 0.2em white; */
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: white;
  background-color: #00afb9;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: lightgray;
    /* color: white; */
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    /* color: white; */
    /* background: transparent; */
    align-items: center;
    justify-content: center;
    background-color: #00afb9;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }

  .dark a {
    color: white;
    text-decoration: none;
    font-weight: bolder;
    text-shadow: 0 0 0.2em black;
  }

  .dark button {
    /* border: 1px solid white; */
    color: white;
    background: transparent;
  }
  .dark a:hover,
  .dark a:active,
  .dark a.active {
    /* background: #f8df00;
    color: #292929; */
    /* background: #183883;
    border-color: #292929;
    color: white;
    font-weight: bolder; */
    /* background: #b6c0d8; */
    color: #f72585;
    font-weight: 1000;
    font-size: large;
    /* text-shadow: 0 0 0.2em white, 0 0 0.2em white; */
  }
}
